<template>
  <div class="user-chat-container">
    <!--  -->
    <van-nav-bar title="" left-arrow @click-left="$router.back()" />
    <!-- / -->

    <!--  -->
    <van-cell-group class="message-list" ref="messages-list">
      <!-- <van-cell v-for="(item, index) in messages" :key="index" :title="item.msg" /> -->
      <div class="message-item" :class="{ reverse: item.userId === currentUser.id }" v-for="(item, index) in messages" :key="index">
        <van-image class="avatar" round fit="cover" :src="item.photo" />
        <div class="msg-wrap" style="flex: 1">
          <div class="title">{{ item.msg }}</div>
        </div>
      </div>
    </van-cell-group>
    <!-- / -->

    <!--  -->
    <div class="send-message-warp">
      <van-field v-model="message" center placeholder="">
        <template #button>
          <van-button size="small" round type="info" :disabled="message === ''" @click="onSend"></van-button>
        </template>
      </van-field>
    </div>
    <!-- / -->
  </div>
</template>

<script>
// 
import { io } from 'socket.io-client'
import { getItem, setItem } from '@/utils/storage'
import { mapState } from 'vuex'
import { getUserProfile } from '@/api/user'

export default {
  name: 'UserChat',
  data() {
    return {
      message: '',
      socket: null, // socket
      messages: getItem('toutiao-chat-messages') || [], // 
      currentUser: {} // 
    }
  },
  computed: {
    ...mapState(['user'])
  },
  created() {
    this.loadCurrentUserProfile()
    // 
    const socket = io('http://toutiao.itheima.net', {
      query: {
        token: this.user.token // token
      },
      transports: ['websocket']
    })
    this.socket = socket
    // 
    // socket.on('connect', () => {
    //   console.log('')
    // })
    // socket.on('disconnect', () => {
    //   console.log('')
    // })

    // 
    socket.on('message', (data) => {
      data.photo = 'https://z3.ax1x.com/2021/11/14/I6gy5D.jpg'
      this.messages.push(data)
    })
  },
  mounted() {
    // 
    this.scrollToBottom()
  },
  methods: {
    // 
    async loadCurrentUserProfile() {
      // 
      if (this.user) {
        const { data } = await getUserProfile()
        this.currentUser = data.data
      }
    },
    // 
    onSend() {
      // 
      // const data = {
      //   msg: this.message,
      //   timestamp: Date.now()
      // }
      console.log(this.user)
      const data = {
        msg: this.message,
        timestamp: Date.now(),
        userId: this.currentUser.id,
        photo: this.currentUser.photo
      }
      this.socket.emit('message', data)
      this.messages.push(data)

      // 
      this.message = ''
    },
    // 
    scrollToBottom() {
      const list = this.$refs['messages-list']
      list.scrollTop = list.scrollHeight
    }
  },
  watch: {
    // messages
    messages() {
      setItem('toutiao-chat-messages', this.messages)
      // DOM
      this.$nextTick(() => {
        // 
        this.scrollToBottom()
      })
    }
  }
}
</script>

<style lang="less" scoped>
.user-chat-container {
  .message-list {
    position: fixed;
    left: 0;
    right: 0;
    top: 46px;
    bottom: 48px;
    overflow-y: auto;
    // 
    .message-item {
      display: flex;
      flex-flow: row nowrap; // flex-flow  flex-direction  flex-wrap  row  nowarp 
      align-items: flex-start; // 
      padding: 10px;
      .msg-wrap {
        word-break: break-all; // word-break 
        word-wrap: break-word; // break-word  URL 
      }
      .title {
        display: table; //  <table>
        background: #e0effb;
        border-radius: 6px;
        font-size: 14px;
        padding: 10px 10px;
        margin-right: 45px;
      }
      .avatar {
        width: 40px;
        height: 40px;
        margin-right: 5px;
      }
    }
    // 
    .reverse {
      flex-direction: row-reverse; //  row 
      .msg-wrap {
        display: flex;
        flex-direction: row-reverse;
        .title {
          margin-right: 5px;
          margin-left: 45px;
        }
      }
    }
  }
  .send-message-warp {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    /deep/.van-cell {
      padding: 8px 10px;
      background-color: #f7f7f7;
    }
    /deep/.van-button--small {
      width: 55px;
      height: 30px;
    }
    /deep/.van-field__control {
      height: 30px;
      background-color: #fff;
      border-radius: 20px;
      padding: 0 10px;
      margin-top: 2px;
    }
  }
}
</style>
